import React from 'react';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import ListSubheader from '@material-ui/core/ListSubheader';
import DashboardIcon from '@material-ui/icons/Dashboard';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import PeopleIcon from '@material-ui/icons/People';
import BarChartIcon from '@material-ui/icons/BarChart';
import LayersIcon from '@material-ui/icons/Layers';
import AssignmentIcon from '@material-ui/icons/Assignment';
import { Link } from 'react-router-dom';

const style = {
  textDecoration: 'none',
  color: 'white'
}


export const mainListItems = (
  <div>
    <ListItem button>
      <ListItemIcon>
        <DashboardIcon style={{ color: 'white' }} />
      </ListItemIcon>
      <Link to='/home/' style={style}> <ListItemText primary="仪表盘" /></Link>
    </ListItem>
    <ListItem button>
      <ListItemIcon >
        <ShoppingCartIcon style={{ color: 'white' }} />
      </ListItemIcon>
      <Link to='/home/device' style={style}> <ListItemText primary="设备管理" /></Link>
    </ListItem>
    <ListItem button>
      <ListItemIcon>
        <PeopleIcon style={{ color: 'white' }} />
      </ListItemIcon>
      <Link to='/home/user' style={style}> <ListItemText primary="用户管理" /></Link>
    </ListItem>
    <ListItem button>
      <ListItemIcon>
        <BarChartIcon style={{ color: 'white' }} />
      </ListItemIcon>
      <ListItemText primary="报表" />
    </ListItem>
    <ListItem button>
      <ListItemIcon>
        <LayersIcon style={{ color: 'white' }} />
      </ListItemIcon>
      <ListItemText primary="机构管理" />
    </ListItem>
  </div>
);

export const secondaryListItems = (
  <div>
    <ListSubheader inset>Saved reports</ListSubheader>
    <ListItem button>
      <ListItemIcon>
        <AssignmentIcon />
      </ListItemIcon>
      <ListItemText primary="Current month" />
    </ListItem>
    <ListItem button>
      <ListItemIcon>
        <AssignmentIcon />
      </ListItemIcon>
      <ListItemText primary="Last quarter" />
    </ListItem>
    <ListItem button>
      <ListItemIcon>
        <AssignmentIcon />
      </ListItemIcon>
      <ListItemText primary="Year-end sale" />
    </ListItem>
  </div>
);