import React from 'react';
import Title from './Title';
import PersonBlock from '../../component/PersonBlock'
import { makeStyles } from '@material-ui/core/styles';
import { findByLabelText } from '@testing-library/react';
import Grid from '@material-ui/core/Grid';
import PersonIcon from '@material-ui/icons/Person';
import GroupIcon from '@material-ui/icons/Group';
import PersonAddIcon from '@material-ui/icons/PersonAdd';

import moreuser from './svg/多用户模式.svg';
import todayuser from './svg/用户.svg'
import newuser from './svg/新增用户.svg'
import warn from './svg/异常.svg'

const useStyles = makeStyles(theme => ({
    root: {
        // display:'flex',
        flexGrow: 1,
        marginTop:15,
        // justifyContent: 'space-between'
    },
    perIcon: {
        margin: 5,
        fontSize: 80
    },

    flex:{
        display:'flex',
        justifyContent: 'center',
        
    }
}));

function App(props) {

    const classes = useStyles();
    return (
        <>
            <Title>数据统计</Title>
            <Grid container className={classes.root} spacing={1} >
                <Grid item xs={3} className={classes.flex} >
                    <PersonBlock title='用户总量' count='123'  icon={( <img src={moreuser} alt="moreuser" />)} />
                </Grid>
                <Grid item xs={3} className={classes.flex}>
                    <PersonBlock title='今日访问量'count='456'   icon={( <img src={todayuser} alt="todayuser" />)} />
                </Grid>
                <Grid item xs={3} className={classes.flex}>
                    <PersonBlock  title='今日新增用户'  count='789'  icon={( <img src={newuser} alt="newuser" />)}/>

                </Grid>
                <Grid item xs={3} className={classes.flex}>
                    <PersonBlock  title='用户异常数据量'  count='789'  icon={( <img src={warn} alt="warn" />)}/>

                </Grid>
            </Grid>



        </>
    );
}

export default App;